.Loading__i-nKt {
  width: 100vw;
  height: 100vh;
  background-color: rgba(150, 150, 150, 0.5);
}
.Loading__i-nKt section {
  text-align: center;
}
.Loading__i-nKt section i {
  display: inline-block;
  font-size: 0.4rem;
  -webkit-animation: round__3XE1Z 1s ease-in-out infinite;
          animation: round__3XE1Z 1s ease-in-out infinite;
}
.Loading__i-nKt section span {
  font-size: 0.14rem;
}
@-webkit-keyframes round__3XE1Z {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes round__3XE1Z {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
